import React from 'react'
import "./Testimonials.css"
import {AiFillStar} from "react-icons/ai"
import {AiOutlineStar} from "react-icons/ai"
import IMG from "../../images/avatar-01.png"
import IMG2 from "../../images/avatar-02.png"
import IMG3 from "../../images/avatar-03.png"
import IMG4 from "../../images/avatar-04.png"
import IMG5 from "../../images/avatar-05.png"
import IMG6 from "../../images/avatar-06.png"

const data =[
  {
    id:1,
    image:IMG,
    name:"John Doe",
    position:"Full Stack Web Developer",
  },
  {
    id:2,
    image:IMG2,
    name:"John Doe",
    position:"Full Stack Web Developer",
  },
  {
    id:3,
    image:IMG3,
    name:"John Doe",
    position:"Full Stack Web Developer",
  },
  {
    id:4,
    image:IMG4,
    name:"John Doe",
    position:"Full Stack Web Developer",
  },
  {
    id:5,
    image:IMG5,
    name:"John Doe",
    position:"Full Stack Web Developer",
  },
  {
    id:6,
    image:IMG6,
    name:"John Doe",
    position:"Full Stack Web Developer",
  },
]


const Testimonials = () => {
  return (
    <div className="testimonials" id="testimonials">
      <div className="content">
      <h2>Testimonials</h2>
      <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Voluptas quidem quos corporis a assumenda minima?</p>
      </div>
      <div className="container">
        {
          data.map(({id,image,name,position}) => {
            return(
              <div key={id} className="box">
              <img src={image} alt="avatar" />
              <h3>{name}</h3>
              <span className="title">{position}</span>
              <div className="rate">
                <AiFillStar  className='icon filled'/>
                <AiFillStar  className='icon filled'/>
                <AiFillStar  className='icon filled'/>
                <AiFillStar  className='icon filled'/>
                <AiOutlineStar  className='icon filled'/>  
              </div>
              <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Doloremque odit, corrupti nulla, beatae quaerat quis fuga optio expedita ipsam accusamus adipisci totam perspiciatis voluptas! Enim odit possimus facilis consequuntur mollitia?</p>
            </div>
            )
          })
        }
      </div>
    </div>
  )
}

export default Testimonials